var React = require("react");
import { Link } from "react-router";

var Header = React.createClass({
  render: function() {
    const {user, logging_in, error} = this.props.currentUser;

    if (user) {
      return (
        <header>
          <div>
            <h1><Link to="/blog">Pepperpapa's Blog</Link></h1>
            <nav>
              <ul>
                <li>
                  <Link to="/blog/newpost" className="link-header"></Link>
                </li>
                <li>
                  <a href="#" className="link-header">{user}</a>
                </li>
                <li>
                  <Link
                    to="/signup"
                    className="link-header"
                    onClick={this.props.logout}></Link>
                </li>
              </ul>
            </nav>
          </div>
        </header>
      );
    } else {
      return (
        <header>
          <div>
            <h1><Link to="/blog">Pepperpapa's Blog</Link></h1>
            <nav>
              <ul>
                <li>
                  <Link to="/login" className="link-header"></Link>
                </li>
                <li>
                  <Link to="/signup" className="link-header"></Link>
                </li>
              </ul>
            </nav>
          </div>
        </header>
      );
    } // end of else
  } // end of render
});

module.exports = Header;



